import { lazy } from "react";
import PrivateRoute from "./PrivateRoute";
import { BrowserRouter as Router, Switch } from "react-router-dom";
const login = lazy(() => import("../pages/login"));
const systmeSet = lazy(() => import("../pages/systemSet"));
const server = lazy(() => import("../pages/server"));
const trajectory = lazy(() => import("../pages/trajectory"));
const TrajectoryDetails = lazy(() => import("../pages/trajectory/details"));
const fence = lazy(() => import("../pages/fence"));
const terminal = lazy(() => import("../pages/server/details"));
const FenceDetails = lazy(() => import("../pages/fence/details"));


export type RouterType = {
  path: string;
  component: React.LazyExoticComponent<any>;
  root: [];
  auth?: boolean;
  notExect?: boolean;
}[];

const Routers: RouterType = [
  {
    path: "/",
    component: systmeSet,
    auth: true,
    root: [],
  },
  {
    path: "/login",
    component: login,
    root: [],
  },
  {
    path: "/server",
    component: server,
    auth: true,
    root: [],
  },
  {
    path: "/server/:id",
    component: terminal,
    auth: true,
    root: [],
  },
  {
    path: "/terminal",
    component: terminal,
    auth: true,
    root: [],
  },
  {
    path: "/trajectory",
    component: trajectory,
    auth: true,
    root: [],
  },
  {
    path: "/trajectory/:id",
    component: TrajectoryDetails,
    auth: true,
    root: [],
  },
  {
    path: "/fence",
    component: fence,
    auth: true,
    root: [],
  },
  {
    path: "/fence/:id",
    component: FenceDetails,
    auth: true,
    root: [],
  },
];

function Routes(props: any) {
  return (
    <Router>
      <Switch>
        {Routers.map((router) => {
          const { path, component } = router;
          return (
            <PrivateRoute
              exact={!router.notExect}
              key={path}
              path={path}
              component={component}
            ></PrivateRoute>
          );
        })}
      </Switch>
    </Router>
  );
}

export default Routes;
