import cookie from 'js-cookie';
//token 处理 添加
export const setToken = (token: string) => cookie.set('Admin_Token_key', token);
//token 处理 获取
export const getToken: () => string = () => cookie.get('Admin_Token_key')  || '';
//token 处理 移除
export const removeToken = () => cookie.remove('Admin_Token_key');
