// 调用异步请求 call、fork
import { Predicate, StringableActionCreator, ActionSubPattern } from "@redux-saga/types";
import { Action } from "redux";
import {
    take,
    //takeEvery,
    call,
    fork,
    put,
    CallEffect,
    PutEffect,
  } from "redux-saga/effects";
  import LoginService from "../service/login";
  import {LOGIN_FAILURE, LOGIN_SAGA, LOGIN_SUCCESS, REQUEST} from "./const";
  
  
  // worker saga generator
  function* loginHandle(action:any):any {
    yield put({type: REQUEST});
  
    // 异步行为
    try {
      const res1 = yield call(LoginService.login, action.payload);
      const res2 = yield call(LoginService.getMoreUserInfo, res1);
      yield put({type: LOGIN_SUCCESS, payload: res2});
    } catch (err) {
      yield put({type: LOGIN_FAILURE, payload: err});
    }
  }
  
  // watcher saga generator
  
  function* loginSaga() {
    yield takeEvery(LOGIN_SAGA, loginHandle);
  }
  
  export default loginSaga;
  
  const takeEvery = (pattern: string | number | symbol | Predicate<Action<any>> | StringableActionCreator<Action<any>> | ActionSubPattern<Action<any>>[] | undefined, saga: (action: any) => Generator<CallEffect<any> | PutEffect<{ type: string; }>, void, unknown>) =>
    fork(function*():any {
      while (true) {
        const action = yield take(pattern);
        yield fork(saga, action);
      }
    });
  