const userInit = {
  isLogin: true,
  userInfo: { id: null, name: "", score: 0 },
  loading: false, // loading
  err: { msg: "" },
};

// 定义用户基本信息修改规则
export const loginReducer = (state = { ...userInit }, { type, payload }) => {
  switch (type) {
    case "REQUEST":
      return { ...state, loading: true };
    case "LOGIN_SUCCESS":
      return {
        ...state,
        isLogin: true,
        loading: false,
        userInfo: { ...payload },
      };
    case "LOGIN_FAILURE":
      return { ...state, ...userInit, ...payload };
    case "LOGOUT_SUCCESS":
      return { ...state, isLogin: true, loading: false };
    default:
      return state;
  }
};
// 定义用户基本信息修改规则
export const headstateReducer = (headState = '/', { type, payload }) => {
  
  switch (type) {
    case "SET_HEAD_STATE":
      return headState = payload;
    default:
      return headState;
  }
};

// 定义用户基本信息修改规则
export const detailsReducer = (detailsInfo = {}, { type, payload }) => {
  switch (type) {
    case "ADD":
      return detailsInfo = payload;
    default:
      return detailsInfo;
  }
};