import { withRouter } from "react-router-dom";
import log from "../assets/images/logo.png";
import icon_tx from "../assets/images/icon-touxian.png";
import icon_back from "../assets/images/icon-back.png";
import { connect } from "react-redux";
import { getToken, removeToken } from "../utils/cookie";
import jwt_decode from 'jwt-decode'

function Header(props: any) {
  const { headState, history } = props;
  const {user} = jwt_decode(getToken()) as {user:string}
  const navList = [
    { name: "系统配置", url: "/" },
    { name: "服务管理", url: "/server" },
    { name: "轨迹管理", url: "/trajectory" },
    { name: "电子围栏管理", url: "/fence" },
  ];
  const reg = new RegExp(headState.split('/')[1]);
  function setHeadStateClassName(to: string) {
    let act = "topTit";
    if ((headState === "/" && to === "/") || (headState !== "/" && reg.test(to))) {
      act = "topTit active";
    }
    return act;
  }
  function backHandle(to:string) {
    if (to === '/login'){
      removeToken()
    } 
    history.push(to);
  }
  return (
    <header>
      <div className="logo">
        <img src={log} alt="" width="150px" onClick={()=>{history.push('/');}} />
      </div>
      <nav>
          {navList.map(n =>(
              <div onClick={()=> {backHandle(n.url)} } className={setHeadStateClassName(n.url)} key={n.url}> {n.name} </div>
          )
          )}
      </nav>
      <div className="headerUser">
        <div>
          <img src={icon_tx} width="20px" height="20px" alt="" />
        </div>
        <div>{user}</div>
        <div className="line"></div>
        <div className="back" onClick={() => backHandle('/login')}>
          <img src={icon_back} width="20px" height="20px" alt="" />
        </div>
      </div>
    </header>
  );
}

export default withRouter(
  connect((headState) => {
    return ({
      ...headState,
    })
  })(Header)
);
