import { Route } from "react-router-dom";
import { connect } from "react-redux";
import { getToken } from '../utils/cookie';
import Header from "../components/Header";
import Footer from "../components/Footer";

function PrivateRoute({ component: Component, ...restProps }: any) {
  const { path, dispatch } = restProps;
  dispatch({ type: "SET_HEAD_STATE", payload: path });
  const isLogin = getToken() || path === '/login' ? true : false
  // console.log(isLogin, getToken(), path === '/login' )
  return (
    <Route
      {...restProps}
      render={(props) => {
        const { match } = props;
        return isLogin ? (
          match.path === "/login" ? (
            <Component {...props} />
          ) : (
            <>
              <Header></Header>
              <Component {...props} />
              <Footer></Footer>
            </>
          )
        ) : props.history.push('./login')
      }}
    />
  );
}
export default connect(
  //mapStateToProps
  ({ user }: any) => ({ isLogin: user.isLogin })
)(PrivateRoute);
